import React, { useState, useEffect } from "react";
import styles from './Diagnosis.module.css';

const choices = [
  { value: 1, label: " " },
  { value: 2, label: "" },
  { value: 3, label: "" },
  { value: 4, label: "" },
  { value: 5, label: " " },
];

function Diagnosis() {
  const [tests, setTests] = useState([]);
  const [selectedTest, setSelectedTest] = useState(null);
  const [questions, setQuestions] = useState([]);
  const [answers, setAnswers] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetch("http://localhost:8080/api/diagnosis/types")
      .then(res => res.json())
      .then(data => {
        setTests(data);
        if (data.length > 0) setSelectedTest(data[0].key);
      });
  }, []);

  useEffect(() => {
    if (!selectedTest) return;
    setLoading(true);
    fetch(`http://localhost:8080/api/diagnosis/questions?type=${selectedTest}`)
      .then(res => res.json())
      .then(data => {
        setQuestions(data);
        setAnswers(Array(data.length).fill(null));
        setLoading(false);
      });
  }, [selectedTest]);

  const handleAnswerChange = (questionIdx, value) => {
    setAnswers(prev =>
      prev.map((ans, idx) => (idx === questionIdx ? value : ans))
    );
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (answers.some(ans => ans === null)) {
      alert("   .");
      return;
    }
    //     . API  .  .
    alert(" .\n(       )");
  };

  return (
    <div className={styles.container}>
      <div className={styles.mainContent}>
        <div className={styles.sidebar}>
          <div className={styles.buttonContainer}>
            {tests.map((test) => (
              <button
                key={test.key}
                className={`${styles.categoryBtn} ${selectedTest === test.key ? styles.categoryBtnActive : ""}`}
                onClick={() => setSelectedTest(test.key)}
              >
                <h3>{test.label}</h3>
                <p>{test.description}</p>
              </button>
            ))}
          </div>
        </div>
        <div className={styles.contentContainer}>
          <div className={`${styles.content} ${styles.contentActive}`}>
            {selectedTest && (
              <>
                <h2>
                  {tests.find(t => t.key === selectedTest)?.label} 
                </h2>
                <p className={styles.contentDescription}>
                  {tests.find(t => t.key === selectedTest)?.description}   .
                </p>
                {loading ? (
                  <div> ...</div>
                ) : (
                  <form onSubmit={handleSubmit}>
                    <ul className={styles.postList}>
                      {questions.map((q, idx) => (
                        <li className={styles.postItem} key={idx}>
                          <div className={styles.postTitle}>{q}</div>
                          <div style={{ marginTop: "10px", marginBottom: "15px" }}>
                            {choices.map(choice => (
                              <label key={choice.value} style={{ marginRight: "15px" }}>
                                <input
                                  type="radio"
                                  name={`question_${idx}`}
                                  value={choice.value}
                                  checked={answers[idx] === choice.value}
                                  onChange={() => handleAnswerChange(idx, choice.value)}
                                />
                                {choice.label}
                              </label>
                            ))}
                          </div>
                        </li>
                      ))}
                    </ul>
                    <button
                      type="submit"
                      className={styles.actionBtn}
                      style={{ marginTop: "20px" }}
                    >
                      
                    </button>
                  </form>
                )}
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Diagnosis;
