import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./Signup.css";

function Signup() {
  const nav = useNavigate();
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    password_confirm: "",
    name: "",
    gender: "",
    nickname: "",
    birth: "",
    phone: "",
  });

  const [emailChecked, setEmailChecked] = useState(false);

  const checkEmailDuplicate = () => {
    const savedUser = sessionStorage.getItem("user");
    const savedEmail = savedUser ? JSON.parse(savedUser).email : null;
    if (formData.email === savedEmail) {
      alert("  .");
      setEmailChecked(false);
    } else {
      alert("  .");
      setEmailChecked(true);

    }
  };

  const onChangeForm = (e) => {
    const { name, value } = e.target;

    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));

    if (name === "email") {
      checkEmailDuplicate(false);
    }
  };

  const onSubmitForm = async (e) => {
    e.preventDefault();

    const { email, password, password_confirm } = formData;

    if (!email) return alert("  ()");
    if (!emailChecked) return alert("   ");
    if (!password) return alert(" ");
    if (!password_confirm) return alert("  ");
    if (password !== password_confirm) return alert("   .");

    try {
      const response = await axios.post(
        "http://localhost:8080/api/users/register",
        {
          userEmail: formData.email,
          password: formData.password,
          userName: formData.name,
          nickname: formData.nickname,
          userBirth: parseInt(formData.birth),
          userGender: formData.gender,
          userPhone: formData.phone,
        }
      );

      const { token, nextStep, userEmail, role, expiresAt } = response.data;

      // JWT  
      sessionStorage.setItem("token", token);
      sessionStorage.setItem("userEmail", userEmail);
      sessionStorage.setItem("role", role);
      sessionStorage.setItem("expiresAt", expiresAt);

      alert(" !");
      nav(nextStep || "/");
    } catch (error) {
      alert(" : " + (error.response?.data?.error || "   "));
      const nextStep = error.response?.data?.nextStep || "/signup";
      nav(nextStep);
    }
  };

  return (
    <div className="signup-container">
      <form className="form-submit" onSubmit={onSubmitForm}>
        <div className="input-box">
          <input
            className="email"
            name="email"
            type="text"
            placeholder=""
            onChange={onChangeForm}
          />
          <button type="button" className="emailChecked" onClick={checkEmailDuplicate}>
            
          </button>
        </div>
        <div className="input-box">
          <input
            className="password"
            name="password"
            type="password"
            placeholder=""
            onChange={onChangeForm}
          />
        </div>
        <div className="input-box">
          <input
            className="password_confirm"
            name="password_confirm"
            type="password"
            placeholder=""
            onChange={onChangeForm}
          />
        </div>
        <div className="input-box">
          <input
            type="text"
            name="name"
            placeholder=""
            onChange={onChangeForm}
          />
        </div>
        <div className="input-box">
          <input
            type="text"
            name="nickname"
            placeholder=""
            onChange={onChangeForm}
          />
        </div>
        <div className="input-box">
          <input
            type="text"
            name="birth"
            placeholder=" 8 (: 19991231)"
            onChange={onChangeForm}
          />
        </div>
        <div className="input-box">
          <input
            type="text"
            name="phone"
            placeholder=""
            onChange={onChangeForm}
          />
        </div>
        <div className="radio-group">
          <label>
            <input
              type="radio"
              name="gender"
              value="M"
              checked={formData.gender === "M"}
              onChange={onChangeForm}
            />
            
          </label>
          <label>
            <input
              type="radio"
              name="gender"
              value="F"
              checked={formData.gender === "F"}
              onChange={onChangeForm}
            />
            
          </label>
        </div>
        <div className="submit-button">
          <button id="register" type="submit"></button>
        </div>
      </form>
    </div>
  );
}

export default Signup;